.prev-next{
    @include flexbox(row, wrap, space-between, flex-start);
    @include ld-margin-bottom(0px);
    @include font-size($font-settings-footer);
    &__left{
        svg {
            height: 15px;
        }

        span {
            padding-bottom: 5px;
        }

        .st0 {
            fill: none;
            stroke: var(--mim-text-color);
            stroke-width: 2;
            stroke-miterlimit: 10;
        }
        a{
            padding-left: 15px;
        }

        &:hover{
            span{
                opacity: 1;
                transition: all 0.3s linear;
            }
        }
    }

    &__right{
        align-items: flex-end;
        a{
            padding-right: 15px;
        }
        span{
            text-align: right;
            padding-bottom: 5px;
        }

        svg {
            height: 15px;
        }

        .st0 {
            fill:none;
            stroke: var(--mim-text-color);
            stroke-width:2;
            stroke-miterlimit:10;
        }

        &:hover{
            span{
                opacity: 1;
                transition: all 0.3s linear;
            }
        }
    }

    &__arrow{
        background-size: 15px auto;
        height: 35px;
        @include flexbox(column, wrap, center, flex-start);
        display: pointer;

        span{
            @include md-max(){
                margin-top: 2px;
            }
        }
    }

    span{
        opacity: 0;
        display: block;
    }

    a{
        display: block;
        height: 100%;
        text-decoration: none;
    }
}
